import bandname from 'bandname';
import { auth, db } from '../firebase';
import { signOut } from '@firebase/auth';
import { alertTimeout, docToJSON, formMap } from '../helpers';
import { account, alert, authenticated, page } from '../stores';
import { deleteDoc, doc, getDoc, setDoc } from 'firebase/firestore';
export async function createAccount(uid) {
    try {
        const docRef = doc(db, 'accounts', uid);
        const accountData = newAccount(uid);
        await setDoc(docRef, accountData);
        account.set(accountData);
        alert.set('Account Created');
        alertTimeout();
    }
    catch (e) {
        console.error(e);
    }
}
export async function deleteAccount(uid) {
    confirm('Your account will be permanently deleted.');
    try {
        await deleteDoc(doc(db, 'accounts', uid));
        await signOut(auth);
        authenticated.set(false);
        page.set('login');
        alert.set('Account Deleted');
        alertTimeout();
    }
    catch (e) {
        console.error(e);
    }
}
export async function getAccount(uid) {
    const docRef = doc(db, 'accounts', uid);
    const docSnap = await getDoc(docRef);
    if (docSnap.exists()) {
        const accountData = docToJSON(docSnap.data());
        account.set(accountData);
    }
    else {
        await createAccount(uid);
    }
}
export async function updateAccount(uid, data) {
    const docRef = doc(db, 'accounts', uid);
    await setDoc(docRef, formMap(data), { merge: true });
    page.set('account');
    alert.set('Account Updated');
    alertTimeout();
}
function newAccount(uid) {
    return {
        uid: uid,
        name: bandname(),
        genres: null,
        location: null,
        links: {
            spotify: null,
            apple: null,
            bandcamp: null,
            soundcloud: null,
            instagram: null,
            youtube: null
        },
        library: [],
        imageURL: null
    };
}
