import halfmoon from 'halfmoon';
import { alert } from './stores';
import { Browser } from '@capacitor/browser';
export async function activePage(pageName) {
    let selected = document.getElementsByClassName(pageName)[0];
    let lastselected = document.getElementsByClassName('selectedPage')[0];
    lastselected && lastselected.classList.remove('selectedPage');
    selected && selected.classList.add('selectedPage');
}
export function alertTimeout() {
    setTimeout(() => {
        alert.set('');
    }, 3000);
}
export function docToJSON(doc) {
    return {
        uid: doc.uid,
        name: doc.name,
        genres: doc.genres,
        location: doc.location,
        links: {
            spotify: doc.links.spotify,
            apple: doc.links.apple,
            bandcamp: doc.links.bandcamp,
            soundcloud: doc.links.soundcloud,
            instagram: doc.links.instagram,
            youtube: doc.links.youtube
        },
        library: doc.library,
        latitude: doc.latitude,
        longitude: doc.longitude,
        imageURL: doc.imageURL
    };
}
export function formMap(data) {
    return {
        name: data.name,
        genres: data.genres
            .toLowerCase()
            .split(',')
            .filter((x) => {
            return x.trim() !== '';
        }),
        location: data.location.toUpperCase(),
        links: {
            spotify: data.spotify,
            apple: data.apple,
            bandcamp: data.bandcamp,
            soundcloud: data.soundcloud,
            instagram: data.instagram,
            youtube: data.youtube
        }
    };
}
export function halfmoonInit() {
    halfmoon.onDOMContentLoaded();
    if (window.matchMedia &&
        window.matchMedia('(prefers-color-scheme: dark)').matches) {
        halfmoon.toggleDarkMode();
    }
}
export function hasLinks(artist) {
    return (artist.links.spotify ||
        artist.links.apple ||
        artist.links.bandcamp ||
        artist.links.soundcloud);
}
export function inArray(x, array) {
    if (array.indexOf(x) !== -1) {
        return x;
    }
}
export async function openSite(url) {
    await Browser.open({ url: url });
}
export const params = new URLSearchParams(window.location.search);
export const nullAccount = {
    uid: null,
    name: null,
    genres: null,
    location: null,
    links: {
        spotify: null,
        apple: null,
        bandcamp: null,
        soundcloud: null,
        instagram: null,
        youtube: null
    },
    library: [],
    imageURL: null
};
export function validate(data) {
    let errors = {
        name: null,
        genres: null,
        spotify: null,
        apple: null,
        bandcamp: null,
        soundcloud: null,
        instagram: null,
        youtube: null
    };
    if (data.name.length < 1) {
        errors['name'] = 'name required';
    }
    if (data.name.length > 40) {
        errors['name'] = 'name too long';
    }
    if (data.genres.length > 40) {
        errors['genres'] = 'genres too long';
    }
    if (data.spotify.length > 100 ||
        (data.spotify && domain(data.spotify) != 'open.spotify.com')) {
        errors['spotify'] = 'spotify link invalid';
    }
    if (data.apple.length > 100 ||
        (data.apple && !domain(data.apple).includes('apple.com'))) {
        errors['apple'] = 'apple link invalid';
    }
    if (data.bandcamp.length > 100 ||
        (data.bandcamp && !domain(data.bandcamp).includes('bandcamp.com'))) {
        errors['bandcamp'] = 'bandcamp link invalid';
    }
    if (data.soundcloud.length > 100 ||
        (data.soundcloud && domain(data.soundcloud) != 'soundcloud.com')) {
        errors['soundcloud'] = 'soundcloud link invalid';
    }
    if (data.instagram.length > 100 ||
        (data.instagram && domain(data.instagram) != 'instagram.com')) {
        errors['instagram'] = 'instagram link invalid';
    }
    if (data.youtube.length > 100 ||
        (data.youtube && domain(data.youtube) != 'youtube.com')) {
        errors['youtube'] = 'youtube link invalid';
    }
    if (!Object.values(errors).some(function (x) {
        return x !== null;
    })) {
        return null;
    }
    return errors;
}
function domain(url) {
    let domain = new URL(url);
    return domain.hostname.replace('www.', '');
}
