<script lang="ts">
  import { onMount } from 'svelte'
  import { account } from '../stores'
  import { validate } from '../helpers'
  import { deleteAccount, updateAccount } from '../api/account'

  let errors = null
  $: errors = errors

  function onSubmit(e) {
    const formData = new FormData(e.target)
    const data: any = {}
    for (let field of formData) {
      const [key, value] = field
      data[key] = value
    }
    errors = validate(data)
    if (!errors) {
      updateAccount($account.uid, data)
    }
  }

  function scrollToTop(node) {
    node.scrollIntoView()
  }
</script>

<div use:scrollToTop class="card border mx-auto my-20">
  <form class="form" on:submit|preventDefault={onSubmit}>
    <p>Artist Name</p>
    <input
      class="form-control"
      type="text"
      name="name"
      maxlength="40"
      bind:value={$account.name}
    />
    {#if errors && errors['name']}
      <p class="badge badge-danger">*{errors['name']}</p>
    {/if}
    <p>Genres</p>
    <input
      class="form-control"
      type="text"
      name="genres"
      maxlength="40"
      placeholder="black metal, dungeon synth"
      bind:value={$account.genres}
    />
    {#if errors && errors['genres']}
      <p class="badge badge-danger">*{errors['genres']}</p>
    {/if}
    <p>Based In</p>
    <input
      class="form-control"
      type="text"
      name="location"
      maxlength="30"
      placeholder="PHILADELPHIA, PA"
      bind:value={$account.location}
    />
    {#if errors && errors['location']}
      <p class="badge badge-danger">*{errors['location']}</p>
    {/if}
    <p>Spotify</p>
    <input
      class="form-control"
      type="text"
      name="spotify"
      maxlength="100"
      placeholder="https://open.spotify.com/..."
      bind:value={$account.links.spotify}
    />
    {#if errors && errors['spotify']}
      <p class="badge badge-danger">*{errors['spotify']}</p>
    {/if}
    <p>Apple Music</p>
    <input
      class="form-control"
      type="text"
      name="apple"
      maxlength="100"
      placeholder="https://music.apple.com/us/artist/..."
      bind:value={$account.links.apple}
    />
    {#if errors && errors['apple']}
      <p class="badge badge-danger">*{errors['apple']}</p>
    {/if}
    <p>Bandcamp</p>
    <input
      class="form-control"
      type="text"
      name="bandcamp"
      maxlength="100"
      placeholder="https://yourband.bandcamp.com/"
      bind:value={$account.links.bandcamp}
    />
    {#if errors && errors['bandcamp']}
      <p class="badge badge-danger">*{errors['bandcamp']}</p>
    {/if}
    <p>SoundCloud</p>
    <input
      class="form-control"
      type="text"
      name="soundcloud"
      maxlength="100"
      placeholder="https://soundcloud.com/..."
      bind:value={$account.links.soundcloud}
    />
    {#if errors && errors['soundcloud']}
      <p class="badge badge-danger">*{errors['soundcloud']}</p>
    {/if}
    <p>Instagram</p>
    <input
      class="form-control"
      type="text"
      name="instagram"
      maxlength="100"
      placeholder="https://www.instagram.com/..."
      bind:value={$account.links.instagram}
    />
    {#if errors && errors['instagram']}
      <p class="badge badge-danger">*{errors['instagram']}</p>
    {/if}
    <p>Youtube</p>
    <input
      class="form-control"
      type="text"
      name="youtube"
      maxlength="100"
      placeholder="https://www.youtube.com/..."
      bind:value={$account.links.youtube}
    />
    {#if errors && errors['youtube']}
      <p class="badge badge-danger">*{errors['youtube']}</p>
    {/if}
    <button class="btn m-auto mt-20 d-block" type="submit">Submit</button>
    {#if errors && errors['genres']}
      <div class="d-flex">
        <p class="badge badge-danger text-center mt-10 m-auto">
          Whoops! Something's not right. Check form for errors.
        </p>
      </div>
    {/if}
  </form>
</div>
<div class="d-flex justify-content-center">
  <a href="/#" class="text-muted" on:click={() => deleteAccount($account.uid)}
    >Delete Account</a
  >
</div>
