<script lang="ts">
  import { activePage } from '../helpers'
  import { getNearbyArtists } from '../api/nearby'

  import ArtistCard from '../components/ArtistCard.svelte'

  let distance = 10
  let loadingText = 'Finding Artists...'
  let promise = getNearbyArtists(distance).catch(() => {
    loadingText = 'Please enable location services'
  })

  activePage('discover')

  function onClick(e, miles: number) {
    let active = document.getElementsByClassName('active')[0]
    active && active.classList.remove('active')
    e.target.classList.add('active')
    promise = getNearbyArtists(miles)
  }
</script>

<h1 class="page-title text-center">Artists Near You</h1>
<div class="d-flex justify-content-center">
  <!-- svelte-ignore a11y-invalid-attribute -->
  <a href="#" on:click={(e) => onClick(e, 10)} class="badge m-5 active"
    >10 miles</a
  >
  <!-- svelte-ignore a11y-invalid-attribute -->
  <a href="#" on:click={(e) => onClick(e, 25)} class="badge m-5">25 miles</a>
  <!-- svelte-ignore a11y-invalid-attribute -->
  <a href="#" on:click={(e) => onClick(e, 50)} class="badge m-5">50 miles</a>
  <!-- svelte-ignore a11y-invalid-attribute -->
  <a href="#" on:click={(e) => onClick(e, 25000)} class="badge m-5"
    >Everywhere</a
  >
</div>
{#await promise}
  <p class="loading">{loadingText}</p>
{:then artists}
  {#if artists && artists.length == 0}
    <p class="text-center">No Artists Found Nearby</p>
  {:else if artists}
    {#each artists as artist}
      <ArtistCard {artist} />
    {/each}
  {/if}
{/await}
